<script>

    export default {
        name : "Home",
        data()
        {
            return {
                
            }
        },
        mounted()
        {
            this.LoadNameAndDesc();
            this.LoadImageAndBackground();
            this.LoadLinks();
        },
        methods :
        {
            LoadNameAndDesc()
            {
                setTimeout(() =>
                {
                    var name = document.querySelector('#homeTextName');
                    name.style.opacity = '1';
                    name.style.transform = 'none';

                    var desc = document.querySelector('#homeTextDesc');
                    desc.style.opacity = '1';
                    desc.style.transform = 'none';
                }, 400);
            },
            LoadLinks()
            {
                document.querySelectorAll('.homeLinksIcon').forEach((item, index) =>
                {
                    setTimeout(() =>
                    {
                        item.style.opacity = '1';
                        item.style.transform = 'none';
                    }, 200 + (index * 250));
                });
            },
            LoadImageAndBackground()
            {
                var allHomeImageBackgrounds = document.querySelectorAll('.homeImageBackground');

                setTimeout(() =>
                {
                    document.querySelector('#homeImage').style.opacity = '1';
                    
                    allHomeImageBackgrounds.forEach((item, index) =>
                    {
                        setTimeout(() =>
                        {
                            item.id = "homeImageBackground" + (index + 1)
                            item.style.opacity = '1';
                        }, 500 + (index * 100));
                    });
                }, 200);
            }
        }
    }

</script>
