import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Projects from '../views/Projects.vue'
import AboutMe from '../views/AboutMe.vue'

Vue.use(VueRouter)

const routes = [
    {
        path : '/',
        name : 'Home',
        component : Home
    },
    {
        path : '/Projects',
        name : 'Projects',
        component : Projects
    },
    {
        path : '/AboutMe',
        name : 'AboutMe',
        component : AboutMe
    }
]

const router = new VueRouter(
    {
        mode : 'history',
        routes
    }
)

export default router
