<script>

    import Home from './views/Home.vue';
    import Projects from './views/Projects.vue';
    import AboutMe from './views/AboutMe.vue';

    export default {
        name : "App",
        data()
        {
            return {
                componentSelected : 'home'
            }
        },
        components : {
            'home' : Home,
            'projects' : Projects,
            'aboutMe' : AboutMe
        },
        mounted()
        {
            var ref = this;

            document.querySelectorAll('.navBarLinks').forEach((item) =>
            {
                item.onclick = function()
                {
                    document.querySelectorAll('.navBarLinks').forEach((item) =>
                    {
                        item.classList.remove('navBarLinkSelected');
                    });

                    item.classList.add('navBarLinkSelected');
                    ref.componentSelected = item.id;
                }
            });
        }
    }

</script>
