<template>
    <div id="projectsPage">
        <div class="container-fluid">
            <div id="projectsContainer">
                <div class="row">
                    <div class="col-md-12">
                        <h2 id="projectsHeader" class="pt-4">Projects</h2>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12">
                        <div id="projectsItemList">
                            <div class="row d-flex align-items-stretch">
                                <div class="col-lg-4 col-md-6 d-flex" v-for="project in projects" v-bind:key="project.id">
                                    <div class="projectsItem w-100">
                                        <div class="projectsItemImageDiv">
                                            <img :src="project.image" class="projectsItemImage">
                                        </div>
                                        <div class="projectsItemContent">
                                            <p class="projectsItemTitle">{{ project.title }}</p>
                                            <p class="projectsItemLinks">
                                                <a v-for="link in project.links" v-bind:key="link.platform" :href="link.url" target="_blank">
                                                    <i v-if="link.platform == 'GitHub'" class="bx bxl-github projectsItemLink"></i>
                                                    <i v-else-if="link.platform == 'DesktopApp'" class="bx bxl-windows projectsItemLink"></i>
                                                </a>
                                            </p>
                                            <p class="projectsItemDescription">{{ project.description }}</p>
                                            <p class="projectsItemTechnologies d-flex flex-wrap">
                                                <i class="projectsItemTechnology" v-for="tech in project.technologies" v-bind:key="tech">{{ tech }}</i>
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

    export default {
        name : "Projects",
        data()
        {
            return {
                projects : 
                [
                    {
                        id : 1,
                        title : 'GzKitchen',
                        description : 'GzKitchen is an application for japanese style restaurant',
                        image : require('../assets/images/GzKitchen.png'),
                        links :
                        [
                            {
                                platform : 'GitHub',
                                url : 'https://github.com/gavrielsatrio/GzKitchen'
                            },
                            {
                                platform : 'DesktopApp',
                                url : 'https://drive.google.com/file/d/19cuK6OhfWe4lzsYhm3GgdpsMcxwPt4HB/view?usp=sharing'
                            }
                        ],
                        technologies : ['C#', 'LINQ']
                    },
                    {
                        id : 2,
                        title : 'Flappy Dragon',
                        description : 'It\'s a flappy bird but it\'s dragon',
                        image : require('../assets/images/Flappy Dragon.png'),
                        links :
                        [
                            {
                                platform : 'GitHub',
                                url : 'https://github.com/gavrielsatrio/GzKitchen'
                            },
                            {
                                platform : 'DesktopApp',
                                url : 'https://drive.google.com/file/d/19cuK6OhfWe4lzsYhm3GgdpsMcxwPt4HB/view?usp=sharing'
                            }
                        ],
                        technologies : ['JavaScript', 'HTML', 'CSS']
                    }
                ]
            }
        },
        mounted()
        {
            this.LoadHeader();
            this.LoadProjectsItem();
        },
        methods :
        {
            LoadHeader()
            {
                setTimeout(() =>
                {
                    var header = document.querySelector('#projectsHeader');
                    header.style.opacity = '1';
                    header.style.transform = 'none';
                }, 200);
            },
            LoadProjectsItem()
            {
                document.querySelectorAll('.projectsItem').forEach((item, index) =>
                {
                    setTimeout(() =>
                    {
                        item.style.opacity = '1';
                        item.style.transform = 'none';
                    }, 200 + (index * 200));
                });
            }
        }
    }

</script>
<style>

    #projectsContainer
    {
        height: 100vh;
        overflow-y: auto;
        overflow-x: hidden;
    }

    #projectsContainer::-webkit-scrollbar
    {
        width: 0px;
    }

    #projectsContainer::-webkit-scrollbar-track
    {
        background-color: none;
        border-radius: 5px;
    }

    #projectsContainer::-webkit-scrollbar-thumb
    {
        background-color: none;
        border-radius: 5px;
    }

    #projectsHeader
    {
        font-family: futura;
        color: white;

        transition: all 1s ease-in-out;
        -webkit-transition: all 1s ease-in-out;
        -moz-transition: all 1s ease-in-out;
        -o-transition: all 1s ease-in-out;
        -ms-transition: all 1s ease-in-out;

        transform: translate(-50px, 0);
        opacity: 0;
    }

    #projectsItemList
    {
        padding-top: 25px;
    }
    
    .projectsItem
    {
        background-color: white;
        border-radius: 10px;
        min-height: 400px;
        font-family: poppins;
        margin-bottom: 25px;
        transform: translate(0px, -100px);
        opacity: 0;
        
        transition: all 1s ease-in-out;
        -webkit-transition: all 1s ease-in-out;
        -moz-transition: all 1s ease-in-out;
        -o-transition: all 1s ease-in-out;
        -ms-transition: all 1s ease-in-out;
    }

    .projectsItemImageDiv
    {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        height: 150px;
        overflow: hidden;
    }

    .projectsItemImage
    {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        width: 100%;
    }

    .projectsItemContent
    {
        padding: 10px 20px;
        font-family: poppins;
    }

    .projectsItemTitle
    {
        font-weight: bold;
        font-size: 20px;
        padding-bottom: 10px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.15);
        margin: 0;
    }

    .projectsItemDescription
    {
        color: rgb(150, 150, 150);
        font-size: 14px;
        overflow-y: auto;
        height: 80px;
    }

    .projectsItemLinks
    {
        margin: 0;
        padding: 10px 0px;
    }

    .projectsItemLink
    {
        font-size: 22px;
        margin-right: 10px;
        color: rgb(204, 41, 62);
    }

    .projectsItemTechnologies
    {
        margin: 0;
        word-wrap: normal;
    }

    .projectsItemTechnology
    {   
        font-size: 16px;
        font-style: normal;
        background-color: lemonchiffon;
        border-radius: 5px;
        padding: 5px 10px;
        margin-right: 15px;
        margin-bottom: 15px;
    }

</style>